import React from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import Aux from '../../hoc/_Aux/_Aux';
import resumePath from '../../assets/images/codyLuke_resume.pdf';

const about = props => {
	return (
		<Aux>
			<Helmet>
				<title>About | Cody</title>
				<meta name="description" content="Dan Beltramo - About Me" />
			</Helmet>
			<div className="animated m-t-150 l-w1">
			<p className="projectCopy blackText aboutMe w2">
		    Cody Luke was born and raised in San Francisco. She is currently based in Portland, Oregon, and is working as a Store Designer for Nike. She is well-versed in developing design concepts and interior details for high-end retail environments. In her free
		    time, she loves to stay up to date with current trends, color, technology, and related design influencers.</p>
				<div className="resume w1 m-t-20 m-b-90">
					<a href={resumePath} target="_blank" rel="noopener noreferrer">
						<p className="pink">Resume</p>
					</a>
				</div>
			</div>
		</Aux>
	);
};

export default about;
